<style lang="less" scoped>
.user-box {
  &:after {
    display: block;
    content: "";
    font-size: 0;
    clear: both;
  }
}
.box-tree {
  float: left;
  width: 356px;
}
.box-list {
  margin-left: 376px;
}
.box-title {
  margin-bottom: 20px;
  padding: 0 12px;
  display: block;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  background-color: #f1f3f5;
  span {
    margin-left: 12px;
    font-size: 12px;
    color: #909399;
  }
}
.tree-wraper {
  padding: 20px;
  border: 1px solid #DCDAE2;
  border-radius: 4px;
}
.uzzy-search {
  display: flex;
  padding-bottom: 10px;
  @{global} {
    .el-button {
      margin-left: 10px;
    }
    .el-autocomplete {
      flex: 1;
      .el-input__inner{
        height: 32px;
        line-height: 32px;
        font-size: 12px;
        padding: 0 10px;
      }
    }
  }
}
.tree-box {
  overflow: auto;
  height: calc(100vh - 188px);
  @{global} {
    .el-tree-node>.el-tree-node__children {
      overflow: inherit;
    }
    .tree-content {
      margin: 0 -14px 0 -6px;
      width: calc(100% + 20px);
    }
  }
}
.filter-tree-box {
  position: relative;
  height: 100%;
}
.li-item {
  margin-bottom: 15px;
  i {
    display: inline-block;
    margin-right: 8px;
    width: 8px;
    height: 8px;
    border-radius: 8px;
    background-color: #333;
  }
  span {
    display: inline-block;
    margin-right: 10px;
    color: #333;
  }
}
.system-items {
  margin: 12px -10px 10px -10px;
  overflow: hidden;
  .system-item {
    float: left;
    margin: 0 10px 12px 10px;
  }
}
.filter-item {
  position: relative;
  width: 200px;
}
</style>
<template>
  <div class="page-box">
    <top-header title="系统权限审计"></top-header>
    <div class="page-container">
      <div class="box-tree">
        <strong class="box-title">系统列表</strong>
        <div class="tree-wraper">
          <div class="tree-box">
            <div class="filter-tree-box">
              <filter-tree @node-click="handleNodeClick" :default-expand-all="true" ref="treeRef" :data="systems" placeholder="请输入系统名称"></filter-tree>
            </div>
          </div>
        </div>
      </div>
      <div class="box-list">
        <strong class="box-title">员工列表<span v-show="currentName">{{'-&nbsp;&nbsp;' + currentName}}</span></strong>
        <div class="user-box">
          <div class="table-filters">
            <div class="filter-item">
              <p class="item">工号</p>
              <el-input v-model="filterCondition.employee_code"
                        clearable
                        size="small"
                        placeholder="请输入"></el-input>
            </div>
            <div class="filter-item">
              <p class="item">姓名</p>
              <el-input v-model="filterCondition.user_name"
                        clearable
                        size="small"
                        placeholder="请输入"></el-input>
            </div>
            <div class="filter-item">
              <p class="item">职位</p>
              <el-input v-model="filterCondition.position"
                        clearable
                        size="small"
                        placeholder="请输入"></el-input>
            </div>
            <div class="filter-item">
              <p class="item">部门</p>
              <el-input v-model="filterCondition.department_name"
                        clearable
                        size="small"
                        placeholder="请输入"></el-input>
            </div>
            <div class='search-btn'>
              <el-button type="primary"
                          size="small"
                          icon="el-icon-search"
                          @click="searchList">查询
              </el-button>
            </div>
          </div>
          <el-table
            :data="tableData"
            size="mini"
            style="width: 100%">
            <el-table-column
              type="index"
              label="序号"
              width="60">
              <template slot-scope="scope">
                {{(page.current - 1) * page.size + scope.$index + 1}}
              </template>
            </el-table-column>
            <el-table-column
              prop="employee_code"
              label="工号">
            </el-table-column>
            <el-table-column
              prop="user_name"
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="position"
              label="职位">
            </el-table-column>
            <el-table-column
              prop="department_name"
              label="部门">
            </el-table-column>
            <el-table-column
              prop="registered_at"
              label="入职时间"
              width="125">
              <template slot-scope="scope">
                {{scope.row.updated_at | datetime}}
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            background
            v-show="page.total > page.sizes[0]"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="page.sizes"
            layout="sizes,prev,pager,next"
            :current-page.sync="page.current"
            :total="page.total"
            prev-text="上一页"
            next-text='下一页'>
          </el-pagination>
        </div>
      </div>
    </div>
 </div>
</template>

<script>
import API from '../../../api'
import TopHeader from '../../../components/header'
import SystemItem from '../../../components/system'
import FilterTree from '../../../components/tree'

export default {
  components: {
    TopHeader,
    SystemItem,
    FilterTree
  },
  data () {
    return {
      // 分页
      page: {
        total: 0,
        size: 10,
        current: 1,
        sizes: [10, 20, 50, 100]
      },
      // 列表数据
      tableData: [],
      // 当前选择查询的用户
      currentName: undefined,
      // 查询条件
      filterCondition: {
        employee_code: undefined, // 工号
        user_name: undefined, // 姓名
        position: undefined, // 职位
        department_name: undefined // 部门
      },

      // 树当前选中的员工
      currentKey: null,
      // 树信息
      systems: []
    }
  },
  methods: {
    // 获取列表数据
    requestList () {
      let params = {
        ...this.filterCondition,
        page: this.page.current,
        per_page: this.page.size
      }
      API.getSystemUsers(this.currentKey, params).then(res => {
        let result = res.result || {}
        this.page.total = result.pagination ? result.pagination.total_count : 0
        this.tableData = result.data || []
      })
    },
    // 查询
    searchList () {
      if (!this.currentKey) {
        this.$message({
          type: 'warning',
          message: '请先选择一个系统!'
        })
        this.$refs.treeRef.$refs.filterInput.focus()
        return
      }
      this.page.current = 1
      this.requestList()
    },
    // 获取每页条数
    handleSizeChange (size) {
      this.page.size = size
      this.requestList()
    },
    // 获取当前页
    handleCurrentChange (index) {
      this.page.current = index
      this.requestList()
    },
    // 获取部门数据
    requestSystemTree () {
      API.searchSys({ per_page: 1000, page: 1 }).then(res => {
        let result = res.result || {}
        let systems = [
          {
            id: -1,
            name: '基本系统',
            children: []
          }, {
            id: -2,
            name: '业务系统',
            children: []
          }
        ]
        let data = result.data || []
        data.forEach(el => {
          if (el.type === 1) {
            systems[0].children.push(el)
          } else {
            systems[1].children.push(el)
          }
        })
        this.systems = systems
      })
    },
    // 点击查询用户
    handleNodeClick (data, node, el) {
      if (node.isLeaf) {
        this.filterCondition.employee_code = undefined
        this.filterCondition.user_name = undefined
        this.filterCondition.position = undefined
        this.filterCondition.department_name = undefined
        this.currentKey = data.id
        this.currentName = data.name
        this.page.current = 1
        this.requestList()
        this.$refs.treeRef.$refs.tree.setCurrentKey(data.id)
      } else {
        this.$refs.treeRef.$refs.tree.setCurrentKey(this.currentKey)
      }
    }
  },
  beforeMount () {
    this.requestSystemTree()
  }
}
</script>



